<template>
    <div class="login-wrap">
        <div class="ms-title">CryptoBox Admin</div>
        <div class="ms-login">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm" v-loading="is_loading" element-loading-text="登录成功,即将跳转">
                <el-form-item prop="name">
                    <el-input v-model="ruleForm.name" placeholder="登录账号"></el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input type="password" placeholder="登录密码" v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')"></el-input>
                </el-form-item>
                <!--<el-form-item >-->
                    <!--<el-input placeholder="uri" v-model="ruleForm.uri"></el-input>-->
                <!--</el-form-item>-->
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                </div>
                <p style="font-size:12px;line-height:30px;color:red;">{{ msg }}</p>
            </el-form>
        </div>
    </div>
</template>

<script>
    import Vue from "vue";
    export default {
        data: function () {
            return {
                ruleForm: {
                    phone: '',
                    password: '',
                    uri:'http://webtoken.dev'
                },
                msg: '',
                is_loading: false,
                rules: {
                    name: [
                        {required: true, message: '请输入用户名', trigger: 'blur'}
                    ],
                    password: [
                        {required: true, message: '请输入密码', trigger: 'blur'}
                    ]
                }
            }
        },
        methods: {
            submitForm(formName) {
                const self = this;
                //主页配置访问地方
//                Vue.prototype.$rootPath = self.ruleForm.uri;
//                Vue.prototype.$apiPath = self.ruleForm.uri + '/back';
//                self.$axios.defaults.baseURL = self.ruleForm.uri + '/back';
                self.$refs[formName].validate((valid) => {
                    if (valid) {
                        self.$axios.post('/auth', self.ruleForm).then((res) => {
                            if (res.data.code == 4000) {
                                self.is_loading = true;
                                localStorage.setItem('user_info', JSON.stringify(res.data.data));
                                self.$router.push('index');
                            } else {
                                self.msg = res.data.msg;
                            }
                        });

                    } else {
                        return false;
                    }
                });
            }
        }
    }
</script>

<style scoped>
    .login-wrap {
        position: relative;
        width: 100%;
        height: 100%;
    }

    .ms-title {
        position: absolute;
        top: 50%;
        width: 100%;
        margin-top: -230px;
        text-align: center;
        font-size: 30px;
        color: #fff;

    }

    .ms-login {
        position: absolute;
        left: 50%;
        top: 50%;
        width: 300px;
        height: 160px;
        margin: -150px 0 0 -190px;
        padding: 40px;
        border-radius: 5px;
        /*background: #fff;*/
    }

    .login-btn {
        text-align: center;
    }

    .login-btn button {
        width: 100%;
        height: 36px;
    }
</style>


